<template></template>
<script>
	export default {
		name: 'BetweenPage',
		computed: {
			code() {
				return window.location.search.replace('?code=', '')
			}
		},
		mounted() {
			this.getAccessToken()
		},
		methods: {
			getAccessToken() {
				this.$api.get('https://github.com/login/oauth/access_token', {
					client_id: '406d2f8f932cc21dc3ce',
					client_secret: '90422725a40a5b07ddfd82947ea1109c668dedf3',
					code: this.code
				}).then((response) => {
					if(typeof response === 'boolean') {
						return false
					}

					this.$storage.set('access_token', response.access_token)
					this.$storage.set('type', response.token_type)

					this.$router.replace({
						name: 'Repositories'
					})
				})
			}
		}
	}
</script>


