<template>
	<div class="repos">
		<div>Мы тут</div>
		<ul>
			<li v-for="repo in repos">{{repo.name}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: "Repositories",
		mounted() {
			this.getRepositories()
		},
		data() {
			return {
				repos: []
			}
		},
		methods: {
			getRepositories() {
				let type = this.$storage.get('type')
				let key = this.$storage.get('access_token')

				this.$api.get('user/repos', {
					"access_token": key
				}).then((response) => {
					this.repos = response
				})
			}
		}
	}
</script>


